<template>
  <div class="row">
    <div class="col-6 relative" id="live">
      <a href="https://www.songkick.com" target="_blank"
        ><img
          class="logo songkick"
          src="../assets/powered-by-songkick-black.png"
      /></a>
      <h1>{{ t("live.title") }}</h1>

      <table class="gigs">
        <tr v-for="gig in gigs" v-bind:key="gig.id" @click="open(gig.uri)">
          <td>{{ formatDate(gig.start.date) }}</td>
          <td>{{ gig.venue.displayName }}</td>
          <td class="city">{{ gig.location.city }}</td>
        </tr>
      </table>
      <span v-if="noGigs()">{{ t("live.nogigs") }}</span>
    </div>
    <div class="col-6 live"><img src="../assets/news.jpg" /></div>
  </div>
</template>

<script>
import { useI18n } from "vue-i18n";
import axios from "axios";

export default {
  name: "Gigs",
  data() {
    return {
      gigs: [
        {
          url: "foo",
          start: {
            date: "2021-01-05",
          },
          venue: {
            displayName: "Foo",
          },
          location: {
            city: "Utrecht",
          },
        },
      ],
    };
  },
  created() {
    this.getGigs();
  },
  setup() {
    return useI18n();
  },
  methods: {
    open(uri) {
      window.open(uri, "_blank").focus();
    },
    formatDate(date) {
      const d = new Date(date);
      const weekday = this.t("weekdays." + d.getDay());
      const month = this.t("months." + d.getMonth());
      const monthday = d.getDate();
      return this.lang == "nl"
        ? weekday + " " + monthday + " " + month
        : weekday + ", " + month + " " + monthday;
    },
    noGigs() {
      return this.gigs == undefined || this.gigs.length == 0;
    },
    getGigs() {
      axios
        .get(
          "https://api.songkick.com/api/3.0/artists/10179135/calendar.json?apikey=VgOeG4TUuURr2Lys"
        )
        .then((data) => {
          this.gigs = data.data.resultsPage.results.event;
        })
        .catch(() => {
          this.gigs = [];
        });
    },
  },
};
</script>

